<template>
	<div class="wrapper">
		<div id="certificate-item" class="certificate">
			<div  class="certificate__wrapper">
				<img class="certificate__img" src="../assets/img/logo.svg" alt="">
				<div class="certificate__header">
					<h5>Certificate of Completion</h5>	
					<p class="meta">Awarded to</p>
				</div>
				<h2 class="bold name">{{fullname}}</h2>
				<div class="certificate__body">
					For completing the Course
					<h4 class="bold">{{title}}</h4>
					<span>(Score: {{score}}%)</span>
				</div>
				<div class="certificate__meta">
					<p>An N-Power Initiative</p>
				</div>
			</div>
		</div><br/>
		<button class="btn__grey" @click="print">Print</button>
	</div>
</template>

<script>
import domToImage from 'dom-to-image';
import * as JsPdf from 'jspdf';

export default {
	name: 'Certificate',
	props: {
		title: {
			type: String,
			default() {
				return '';
			}
		},
		score: {
			type: Number,
			default() {
				return null;
			}
		}
	},
	data() {
		return {
			imgUrl: '',
			fname: this.$session.get('fname'),
			lname: this.$session.get('lname')
		};
	},
	computed: {
		fullname() {
			return `${this.fname} ${this.lname}`;
		}
	},
	mounted() {
		
	},
	methods: {
		print() {
			domToImage.toPng(document.getElementById('certificate-item'), { quality: 0.95 })
				.then((dataUrl) => {
					this.imgUrl = dataUrl;
					const doc = new JsPdf('p', 'pt', 'a4');
					doc.addImage(dataUrl, 'PNG', 30, 30, 500, 350);
					doc.save('certificate.pdf');
				})
				.catch((error) => {
					return error;
				});
			return true;
		}
	}
};
</script>

