		if (this.userObject.is_credential_uploaded === 0) {
			this.showVideo();
		}
		
		window.onYoutubeIframeAPIReady = () => {
			this.player = new YT.Player('youtube-embed', {
				events: { 
					onReady: window.onPlayerReady,
					onStateChange: window.onPlayerStateChange 
				}
			});
		};
		window.onPlayerStateChange = (e) => {
			if (e.data === 0) {
				this.closeVideo();
			}
		};
	},
	created() {
		this.getUserCredentials();
		if (this.userObject.is_credential_uploaded === 1 && this.userObject.is_psychometric_completed === 1) {
			this.getUserProfile();
			this.getCertificates();
			this.getUserJobs();
			eventHub.$on('credential-selected', (payload) => {
				this.selectedCredential = payload;
			});
			return true;
		}
		return false;
	},
	methods: {
		showVideo() {
			const vid = this.$refs.youtubeEmbed.src;
			this.$refs.youtubeEmbed.src = `${vid}&autoplay=1`;
			this.$refs.vidWrap.style.display = 'block';
		},
		closeVideo() {
			const vid = this.$refs.youtubeEmbed.src;
			const resetVid = vid.replace('&autoplay=1', '');
			this.$refs.youtubeEmbed.src = resetVid;
			this.$refs.vidWrap.style.display = 'none';
		},
		setCertificateData(data) {
			this.certificateData = {};
			this.certificateData.title = data.class.title;
			this.certificateData.score = data.score;
			this.showCertificate = true;
		},
		getCertificates() {
			getUserCertificates(this.$session.get('id'))
				.then((response) => {
					if (response.status === 200) {
						this.certificatesLoaded = true;
						this.certificates = response.data.data;
						return true;
					}
					return false;
				})
				.catch((error) => {
					this.certificatesLoaded = false;
					return error;
				})
				.finally(() => {
					this.loadingCertificates = false;
				});
			return true;
		},
		getUserCredentials() {
			this.loadingCredentials = true;
			getUserCredentials(this.$route.params.id)
				.then((response) => {
					if (response.status === 200) {
						this.credentialsList = response.data.data;
						this.credentialsLoaded = true;
						return true;
					}
					return false;
				})
				.catch((error) => {
					this.credentialsLoaded = false;
					return error;
				})
				.finally(() => {
					this.loadingCredentials = false;
				});
		},
		uploadCredential() {
			this.isUploadingCredential = true;
			this.credentialUploaded = false;
			const credentialsItem = {
				user_id: this.$route.params.id, 
				title: this.credentialData.title,
				file: this.credentialData.file
			};
			this.validate(credentialsItem, user.credentials);
			if (Object.keys(this.issues).length > 0) {
				this.errors = this.issues;
				this.isUploadingCredential = false;
				return false;
			}
			uploadCredential(this.$route.params.id, credentialsItem)
				.then((response) => {
					if (response.status === 201) {
						this.credentialsList.push({ ...response.data, status: 0 });
						this.credentialUploaded = true;
						this.updateUserObject({ is_credential_uploaded: 1 });
						this.credentialData = {};
						this.errors = {};
						return true;
					}
					this.credentialUploaded = false;
					return false;
				})
				.catch((error) => {
					this.credentialUploaded = false;
					return error;
				})
				.finally(() => {
					this.isUploadingCredential = false;
				});
			return true;
		},
		addCredential() {
			const img = this.$refs.credentials.files[0];
			const imgSize = 3 * (2 ** 20);
			
			if (img && img.type.match(/image\/*/) && (img.size > imgSize)) {
				errors.credentials = 'File size greater than 3MB';
				return false;
			}
			const reader = new FileReader();
			reader.onload = (e) => {
				this.credentialData.file = e.target.result;
			};
			reader.readAsDataURL(img);
			return true;
		},
		uploadAvatar(file) {
			this.avatarUploaded = false;
			updateAvatar(this.$route.params.id, file)
				.then((response) => {
					if (response.status === 200) {
						this.itemData.avatar = file;
						this.avatarUploaded = true;
						return true;
					}
					this.avatarUploaded = false;
					return false;
				})
				.catch((error) => {
					this.avatarUploaded = false;
					return error;
				})
				.finally(() => {
					this.isUploadingAvatar = false;
				});
			return true;
		},
		/**
        * Checks for image format and if image size < 3MB
        */
		changeAvatar() {
			this.isUploadingAvatar = true;
			const img = this.$refs.avatar.files[0];
			const imgSize = 3 * (2 ** 20);
			
			if (img && img.type.match(/image\/*/) && (img.size > imgSize)) {
				errors.avatar = 'File size greater than 3MB';
				return false;
			}
			const reader = new FileReader();
			reader.onload = (e) => {
				this.avatar = e.target.result;
				this.uploadAvatar(this.avatar);
			};
			reader.readAsDataURL(img);
			return true;
		},
		getUserProfile() {
			this.loadingUser = true;
			getUserProfile(this.$route.params.id)
				.then((response) => {
					if (response.status === 200) {
						this.itemData = response.data.data;
						this.courses = response.data.data.classes;
						this.bank = response.data.data.bank;
						return true;
					}
					this.userLoaded = false;
					return false;
				})
				.catch((error) => {
					this.userLoaded = false; 
					return error;
				})
				.finally(() => {
					this.loadingUser = false;
				});
		},
		updateUser() {
			const result = this.itemData;
			['created_at', 'bank', 'category', 'updated_at', 'avatar', 'id', 'active', 'badges', 'admin', 'credentials', 'classes', 'is_bank_updated', 'is_career_selected', 'is_credential_uploaded', 'is_psychometric_completed'].forEach((key) => {
				delete result[key];
			});

			/**
             * Validate input
             */
			this.validate(result, user.update);
			if (Object.keys(this.issues).length > 0) {
				this.errors = this.issues;
				return false;
			}
			updateUser(this.$route.params.id, this.itemData)
				.then((response) => {
					if (response.status === 200) {
						success({
							title: 'Success! Account has been successfully updated.'
						});
					}
					this.loading = false;
				})
				.catch((error) => {
					fail({
						title: 'Oops! Something went wrong',
						text: error.data
					});
					this.loading = false;
				});
			return true;
		},
		credentialStatus(statusNo) {
			const status = statusNo === 0 ? 'Unapproved' : 'Approved';
			return status;
		},
		updateBank() {
			const result = this.bank;
            
			['created_at', 'updated_at', 'id', 'user_id'].forEach((key) => {
				delete result[key];
			});
			this.validate(result, user.bank);
			if (Object.keys(this.issues).length > 0) {
				this.errors = this.issues;
				return false;
			}

			updateBank(this.$route.params.id, result)
				.then((response) => {
					if (response.status === 200) {
						success({
							title: 'Success! Bank details has been successfully updated.'
						});
					}
					this.loading = false;
				})
				.catch((error) => {
					fail({
						title: 'Oops! Something went wrong',
						text: error.data
					});
					this.loading = false;
				});
			return true;
		},
		selectCredential(item) {
			eventHub.$emit('credential-selected', item);
			this.showCredential = true;            
		},
		deleteCredential(item) {
			deleteCredential(item.id, this.$route.params.id)
				.then((response) => {
					if (response.status === 200) {
						success({
							title: 'Success! Credential has been deleted!'
						});
						this.credentialsList.splice(this.credentialsList.indexOf(item), 1);
						return true;
					}
					return false;
				})
				.catch((error) => {
					fail({
						title: 'Oops! Something went wrong',
						text: error.data
					});
					return false;
				});
			return true;
		},
		getUserJobs() {
			getUserJobs(this.userObject.id)
				.then((response) => {
					if (response.status === 200) {
						this.jobs = response.data.data;
						this.jobsLoaded = true;
						return true;
					}
					this.jobsLoaded = false;
					return false;
				})
				.catch((error) => {
					this.jobsLoaded = false;
					return error;
				})
				.finally(() => {
					this.loadingJobs = false;
				});
			return true;
		},
		...mapMutations([
			'updateUserObject'
		])
	},
	computed: {
		...mapGetters([
			'userObject'
		]),
		fullName() {
			return `${this.userObject.fname} ${this.userObject.lname}`;
		}
	},
	components: {
		Loader,
		Certificate,
		CredentialModal,
		Reports
	},
	filters: {
		date(value) {
			return moment(value).fromNow();
		}
	}
};
</script>

